  right: 20px;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
  transition: all 500ms ease-in-out; }
  .main-nav__toggle::before {
    position: absolute;
    top: -8px;
    left: 0;
    z-index: 10;
    content: "";
    width: 23px;
    height: 5px;
    background-color: #81b3d2;
    cursor: pointer;
    transition: all 500ms ease-in-out; }
  .main-nav__toggle::after {
    position: absolute;
    top: 8px;
    left: 0;
    z-index: 10;
    content: "";
    width: 23px;
    height: 5px;
    background-color: #81b3d2;
