.button {
	display: block;
	min-height: 25px;
	margin-left: auto;
	margin-right: auto;
	padding-top: 15px;
	border: none;
	font-family: $page-font;
	font-size: 21px;
	line-height: 26px;
	color: $white;
	font-weight: 700;
	text-transform: uppercase;
	vertical-align: middle;
	background-color: $main-blue;
	cursor: pointer;
	&:hover, &:focus {
		outline: none;
		background-color: #669ec0;
	}
	&:active {
		color: rgba(255, 255, 255, 0.3);
		background-color: #5496bd;
	}
	&:disabled {
		background-color: #e5e5e5;
	}
}
