.form-radio {
	margin: 0;
	padding: 0;
	width: 280px;
	min-height: 58px;
	margin-bottom: 9px;
	text-transform: none;
}
.form-radio__label {
	position: relative;
	padding-left: 79px;
	font-size: 18px;
	line-height: 26px;
	font-weight: bold;
	color: $main-text-color;
	&::before {
		position: absolute;
		content: "";
		width: 55px;
		height: 55px;
		background-color: #f2f2f2;
		top: -14px;
		left: 1px;
		border-radius: 50%;
	}
	&:hover::before {
		background-color: #e5e5e5;
	}
	&:active::before {
		background-color: #d5d5d5;
	}
}
.form-radio__input:focus+.form-radio__label::before {
	background-color: #e5e5e5;
}
.form-radio__input:checked~.form-radio__label::after {
	position: absolute;
	content: "";
	width: 20px;
	height: 20px;
	background-color: #333333;
	top: 4px;
	left: 19px;
	border-radius: 50%;
}
.form-radio__input:disabled~.form-radio__label::after {
	position: absolute;
	content: "";
	width: 20px;
	height: 20px;
	background-color: #a4a4a4;
	top: 4px;
	left: 19px;
	border-radius: 50%;
}
.form-radio__input:checked~.form-radio__label:active::after {
	background-color: #a4a4a4;
}
@media (min-width: $tablet-width) {
	.form-radio {
		margin-bottom: 20px;
	}
	.form-radio__label {
		padding-left: 75px;
	}
}
@media (min-width: $desktop-width) {
	.form-radio__label {
		padding-left: 79px;
	}
}
