.form-chbx {
	position: relative;
	margin: 0;
	padding: 0;
	width: 280px;
	min-height: 58px;
	margin-bottom: 22px;
	text-align: left;
}
.form-chbx__label {
	position: relative;
	padding-left: 79px;
	font-size: 18px;
	line-height: 26px;
	font-weight: bold;
	color: #000000;
	text-transform: none;
	&::before {
		position: absolute;
		content: "";
		width: 55px;
		height: 55px;
		top: -14px;
		left: 1px;
		background-color: #f2f2f2;
		border-radius: 10%;
	}
	&:hover::before {
		background-color: #e5e5e5;
	}
	&:active::before {
		background-color: #d5d5d5;
	}
}
.form-chbx__input:focus+.form-chbx__label::before, .form-chbx__input:hover+.form-chbx__label::before, {
	background-color: #e5e5e5;
}
.form-chbx__input:checked~.form-chbx__label::after {
	position: absolute;
	content: "";
	top: 6px;
	left: 15px;
	width: 23px;
	height: 16px;
	background: url("../img/icon-tick.svg") no-repeat;
	background-size: 23px 16px;
}
.form-chbx__input:disabled~.form-chbx__label::after {
	position: absolute;
	content: "";
	top: 4px;
	left: 12px;
	width: 23px;
	height: 16px;
	background: url("../img/icon-tick.svg") no-repeat;
	background-size: 23px 16px;
}
.form-chbx__input:active~.form-chbx__label::before {
	background-color: #d5d5d5;
}
.form-chbx__input:active~.form-chbx__label::after {
	opacity: 0.3;
}
.form-chbx--rocks {
	order: 4;
}
@media (min-width: $tablet-width) {
	.form-chbx {
		width: 230px;
		margin-bottom: 31px;
		margin-right: 28px;
	}
}
@media (min-width: $desktop-width) {
	.form-chbx {
		margin-right: 53px;
		width: 205px;
		padding-left: 0;
	}
	.form-chbx:nth-child(4n-1) {
		margin-right: 34px;
	}
	.form-chbx:nth-child(4n) {
		margin-right: 0;
	}
}
